export const help_dataset = "Select the type of data you want to display on the map. “IBD” (identical-by-descent, currently the only available option) will display data about genetic relationships between UK post codes in the past 300 to 1500 years (depending on the value of “Time threshold”, which you can set below). The intensity of the color will reflect how close the genetic relationships are between the selected region (in red) and all other regions. Regions always colored in white are post codes for which not enough data is available for this analysis. Move the mouse over a specific region to highlight it in green. Additional data can be found in the box below. Use “Show advanced” to reveal additional parameters."

export const help_datatype = "Select what specific feature of genetic ancestry you want to display on the map. If you select \"percent shared genome\", the colors and numbers will reflect the percentage of genome that is shared identical-by-descent (IBD) for two average individuals from the selected regions between present day and the selected time threshold. For instance, 6.6% of the genome of two individuals from Lerwick (ZE) is co-inherited from common ancestors who lived between today and 1,500 years ago (this number is usually very small, as only a small fraction of genome is usually co-inherited from ancestors living in the recent centuries). If you select \"number of ancestors\", the map will reflect the number of shared genetic ancestors two individuals from the selected regions share on average between today and the selected time threshold. For instance, two individuals from Llandudno (LL) have on average 2.6 shared genetic ancestors in the past 1,500 years, but an individual from Llandudno shares on average less than one (0.87) common genetic ancestor in the past 1,500 years with an individual from the neighboring region of Chester (CH) (the number of shared genetic ancestors in the recent centuries is also usually quite small)."

export const help_mode = "This option lets you select the color range used in the map, which enables magnifying ancestry patterns in a specific interval of genetic relatedness. Clicking on different regions, you will notice that the strongest genetic relationships are always found between pairs of individuals living in the same post code. For instance, individuals from Birmingham (B) have closer genetic ties with other individuals from Birmingham than with individuals from any other post code. The same is true for all regions, including cosmopolitan areas such as London. This demonstrates that there has been limited movement of genetic ancestors (\"gene flow\") during recent centuries (\"isolation by distance\"). If the color range is set such that the brightest color reflects the sharing of no common ancestry (i.e. no IBD sharing) and the darkest color is set to reflect the largest sharing of ancestry found in the data set for the selected region, the map will become very light for all but the selected region. This makes it hard to detect any ancestry sharing patterns other than the sharing within post codes (you may try this by manually changing the color range to minimum and maximum allowed values after selecting the \"set by user\" option). To help magnify patterns of intermediate genetic relatedness across regions, you may select \"second largest\", or \"95% percentiles\". If you select \"second largest\", the lightest color will represent the sharing of 0 ancestors (and thus 0% of the genome), while the darkest color will represent the second largest value found in the data set for the selected region. Selecting \"95% percentiles\" ignores the bottom 5% and the top 5% regions, setting the lightest color to the 5th percentile of IBD sharing for a region, and the darkest color to the 95th percentile. These options help magnifying interesting ancestry patterns in the data."


